<!-- 视频监控 -->
<template>
  <van-row class="dt-home">
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">视频监控</p>
      </van-col>
    </van-row>

    <van-row>
      <van-form ref="form"
                v-model="form">
        <van-col span="16">
          <van-field v-model="form.DeviceName"
                     class="dt-input"
                     :left-icon="require('@/assets/img/搜索.png')"
                     placeholder="请输入设备名称" />
        </van-col>
        <van-col span="4">
          <van-button native-type="submit"
                      class="dt-button"
                      @click="getEquipmentList">搜索
          </van-button>
        </van-col>
        <van-col span="4">
          <div class="dt-saixuan">
            <img class="dt-saixuan-img"
                 @click="popupShow=true"
                 :src="require('@/assets/img/筛选.png')">
          </div>
        </van-col>
      </van-form>
    </van-row>
    <van-row class="dt-content">
      <van-row style="height:100%">
        <videoSurveillance :mapCenter="mapCenter"
                           :marketList="marketList"
                           :areas="areass"
                           @getDeviceId="getDeviceId">
        </videoSurveillance>
      </van-row>
      <!-- <van-row>
        <van-row>
          <van-image :src="require('../../../assets/img/视频.png')"
                     width="159"
                     height="159"
                     class="dt-image">
          </van-image>
        </van-row>
        <van-row class="dt-font">未选择监控设备</van-row>
      </van-row> -->
    </van-row>
    <!-- 弹出层 -->
    <van-popup v-model="popupShow"
               position="right"
               class="dt-popup">
      <van-row class="dt-pop-title-main">
        <span>筛选</span>
      </van-row>
      <van-row class="dt-pop-title">
        <span>选择区域</span>
      </van-row>
      <van-row>
        <van-field readonly
                   clickable
                   :value="areaName"
                   placeholder="请选择"
                   @click="showPicker = true" />
      </van-row>
      <van-row class="dt-botton-box">
        <div class="dt-bottons">
          <van-button round
                      class="dt-pop-button1"
                      type="info"
                      @click="filtrateCancel">取消</van-button>
          <van-button round
                      class="dt-pop-button2"
                      type="info"
                      @click="filtrateConfirm">确定</van-button>
        </div>
      </van-row>
    </van-popup>
    <van-popup v-model="showPicker"
               round
               position="bottom">
      <van-picker show-toolbar
                  :columns="areaList"
                  @cancel="showPicker = false"
                  @confirm="onConfirm" />
    </van-popup>
    <van-popup v-model="equipmentPop"
               position="left"
               style="width:80%;height:100%;">
      <div style="padding: 50px 0">
        <div class="equipment-box"
             v-for="item in marketList"
             :key="item.DeviceId"
             @click="getDeviceId(item.DeviceId)">{{item.id}}</div>
      </div>

    </van-popup>
    <div class="ico-box"
         @click="clickIco">
      <van-icon name="play-circle-o"
                size="50" />
    </div>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import {
  GetRegionInfos, //获取区域列表
  GetEquipmentPagingList, // 获取设备列表
} from "@/api/mapApi";
import {
  hikVideo,
} from "@/api/hikvision";
import videoSurveillance from "@/components/map/videoSurveillance";
import { getLonLat } from "@/utils/map";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    videoSurveillance,
  },
  data () {
    //这里存放数据
    return {
      form: {
        DeviceName: '',
        // DeviceId: "037bd46ab2c947d3826ee9534466adf8",
        EquipmentType: "Monitor",
        RegionId: '',
        PageSize: 99999,
        PageIndex: 1
      },
      // 区域选择器
      areaName: '',
      areaList: [],
      areasObj: {},
      showPicker: false,
      popupShow: false, // 弹出层是否显示
      equipmentPop: false,
      // 设备输入框
      equipmentName: '',
      // 地图组件
      mapCenter: { lat: 32.331423245139014, lng: 119.08775502223207 },
      formList: {},
      marketList: [],
      video: null,
      hls: null,
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.getAreasList()
    this.getEquipmentList()

    window.clearToast = function () {
      console.log("clearToast");
      this.$toast.clear()
    }
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () {
    this.$toast.clear()
  }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    //获取区域列表
    getAreasList () {
      GetRegionInfos({}).then(response => {
        let areas = response.Data
        for (let index in areas) {
          let area = areas[index]
          this.areaList.push(area.RegionName)
          this.areasObj[area.RegionName] = {
            RegionId: area.RegionId,
            Areas: area.Areas
          }
        }
      })
    },
    // 获取设备列表
    getEquipmentList () {
      console.log("设备列表请求参数", this.form);
      GetEquipmentPagingList(this.form).then(response => {
        console.log("设备列表返回!!!!", response)
        let PageContents = response.Data.PageContents
        PageContents = getLonLat(PageContents)
        this.marketList = []
        let marketList = []
        for (let index in PageContents) {
          let DeviceName = PageContents[index].DeviceName
          let Latitude = PageContents[index].Latitude
          let Longitude = PageContents[index].Longitude
          let DeviceId = PageContents[index].DeviceId
          let info = {
            id: DeviceName,
            lat: Latitude,
            lng: Longitude,
            DeviceId: DeviceId,

          }
          marketList.push(info)
        }
        this.marketList = marketList
        console.log("设备列表", this.marketList);
      })
    },
    // 播放视频
    getDeviceId (DeviceId) {
      // let DeviceId = "92f7aab100ce4c2ab07e14c4822576df"
      console.log("设备id", DeviceId);

      let formData = new FormData()
      formData.append('cameraId', DeviceId)
      formData.append('protocol', "hls")
      hikVideo(formData).then(res => {
        let url = res.data.url
        if (window.webkit) {
          // window.webkit.messageHandlers.avplayer.postMessage(url);
          window.location.href = url
        } else {
          this.$router.push({ name: "OTplayVideo", query: { url: url } })
        }
      }).catch(err => {
        console.log("err" + err);
      })

    },

    clickIco () {
      this.equipmentPop = true
    },

    // 返回首页按钮
    Home () {
      this.$router.push({ name: 'Home' })
    },
    // 选择区域确定事件
    onConfirm (value) {
      this.areaName = value;
      this.form.RegionId = this.areasObj[value].RegionId
      this.showPicker = false;
      console.log("选择的区域名称", this.areaName, this.areasObj[value]);
    },
    // 查看按钮事件
    query () {
      let value = this.areaName;
      this.mapCenter = this.formList[value]
      console.log("查看,区域:", this.areaName, "名称:", this.equipmentName);
    },
    // 筛选确定按钮
    filtrateConfirm () {
      console.log("筛选了");
      this.areass = this.areassw
      this.popupShow = false
      this.getEquipmentList()

    },
    // 筛选取消按钮
    filtrateCancel () {
      this.popupShow = false
      this.areaName = ''
      this.form.RegionId = ''

    },
    back () {
      this.$store.commit("active", 0)
      // history.back(-1);
      this.$router.replace({ name: 'Home' })
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../../styles/dimens';
$noticeBarHeight: px2rem(50);
