  }
  .dt-botton-box {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: px2rem(80);
    border-top: solid px2rem(1) #eaeef2;
    .dt-bottons {
      position: absolute;
      bottom: 0;
      right: 0;
      height: px2rem(80);
      display: flex;
      align-items: center;
      .dt-pop-button1 {
        width: px2rem(80);
        height: px2rem(30);
        padding: 0;
        margin-right: px2rem(18);
        background: #ffffff;
        border: px2rem(1) solid #5870f8;
        border-radius: px2rem(15);
        font-size: px2rem(13);
        font-family: Source Han Sans CN;
        font-weight: 400;
        line-height: px2rem(30);
        color: #5870f8;
      }
      .dt-pop-button2 {
        width: px2rem(80);
        height: px2rem(30);
        padding: 0;
        margin-right: px2rem(23);
        background: #5870f8;
        border-radius: px2rem(15);
        font-size: px2rem(13);
        font-family: Source Han Sans CN;
