<!-- 视频监控组件 -->
<template>
  <div class="dt-main">
    <div id='equMon'></div>
    <!-- <div class="dt-checkBox">
      <van-checkbox v-model="checked1"
                    @click="check1"
                    icon-size="15px">摄像头</van-checkbox>
      <van-checkbox v-model="checked2"
                    @click="check2"
                    icon-size="15px">广播</van-checkbox>
    </div> -->
  </div>
</template>
<script>
var map
var cameraMarker
let infowindow
export default {
  props: ['mapCenter', 'marketList', 'areas'],
  data () {
    //这里存放数据
    return {
      checked1: true,
      checked2: true,
      checked3: true,
      checked4: true,
      childMapCenter: {},
      coordinate: [],
      point: [],
      markList: []
    };
  },
  //监控data中的数据变化
  watch: {
    mapCenter (newValue) {
      this.childMapCenter = newValue
      this.$forceUpdate()
    },
    areas (n) {
      this.coordinate = n
      // this.setpoint()
    },
    marketList (newValue) {
      this.markList = newValue
      this.set()
      this.$forceUpdate()
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.childMapCenter = this.mapCenter
    this.coordinate = this.areas
    this.markList = this.marketList
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.initMap()
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () {
    this.setMapCenter()
  },
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发\

  //方法集合
  methods: {
    setpoint () {
      for (let index in this.coordinate) {
        let lat = this.coordinate[index].Latitude
        let lon = this.coordinate[index].Longitude
        let latlon = new window.TMap.LatLng(lat, lon)
        this.point.push(latlon)
      }
    },
    initMap () {
      //定义地图中心点坐标
      var center = new window.TMap.LatLng(this.childMapCenter.lat, this.childMapCenter.lng)

      //定义map变量，调用 TMap.Map() 构造函数创建地图
      map = new window.TMap.Map(document.getElementById('equMon'), {
        center: center,//设置地图中心点坐标
        zoom: 14.0,   //设置地图缩放级别
        viewMode: '2D'
      });
      // 定义摄像头点标记
      cameraMarker = new window.TMap.MultiMarker({
        id: 'shexiangtou',
        map: map,
        //样式定义
        styles: {
          //创建一个styleId为"myStyle"的样式（styles的子属性名即为styleId）
          "myStyle": new window.TMap.MarkerStyle({
            "width": 21,  // 点标记样式宽度（像素）
            "height": 25, // 点标记样式高度（像素）
            "src": require('../../assets/img/摄像头图标.png'),  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: 17, y: 17 }
          })
        },
        //点标记数据数组
        geometries: this.getGeometries()
      })
      let self = this
      //监听回调函数（非匿名函数）
      var clickHandler = function (evt) {
        console.log("视频id", evt.geometry.properties.DeviceId);
        self.$emit("getDeviceId", evt.geometry.properties.DeviceId)

        if (infowindow) {
          //设置infoWindow
          infowindow.open(); //打开信息窗
          infowindow.setPosition(evt.geometry.position);//设置信息窗位置
          infowindow.setContent(evt.geometry.position.toString());//设置信息窗内容
          infowindow.close();//初始关闭信息窗关闭
        }
        var infoWindowLocation = evt.geometry.position;//创建一个坐标
        //创建InfoWindow实例，并进行初始化
        infowindow = new window.TMap.InfoWindow({
          map: map,
          content: evt.geometry.properties.title, //信息窗口内容
          position: infoWindowLocation,//显示信息窗口的坐标
          offset: { x: 0, y: -17 } //设置信息窗相对position偏移像素，为了使其显示在Marker的上方
        });
      }

      //cameraMarker
      cameraMarker.on("click", clickHandler)

    },
    getGeometries () {
      let list = []
      for (let index in this.markList) {
        let market = this.markList[index]
        list.push({
          "id": market.id,   //点标记唯一标识，后续如果有删除、修改位置等操作，都需要此id
          "styleId": 'myStyle',  //指定样式id
          "position": new window.TMap.LatLng(market.lat, market.lng),  //点标记坐标位置
          "properties": {//自定义属性
            "title": market.id,
            "DeviceId": market.DeviceId
          }
        })
      }
      return list
    },
    // 地图按钮
    check1 () {
      if (this.checked1) {
        cameraMarker.add(this.getGeometries())

      } else {
        cameraMarker.setGeometries([])
        if (infowindow) {
          infowindow.close();//初始关闭信息窗关闭
        }
      }
    },
    check2 () {
    },
    setMapCenter () {
      var center = new window.TMap.LatLng(this.childMapCenter.lat, this.childMapCenter.lng);
      map.setCenter(center)
    },
    set () {
      cameraMarker.setGeometries([])
      cameraMarker.add(this.getGeometries())
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../styles/dimens';
.dt-main {
  position: relative;
  width: 100%;
  height: 100%;
  #equMon {
    width: 100%;
    height: 100%;
  }
  .dt-checkBox {
    display: inline-block;
    position: absolute;
    z-index: 1000;
    right: 0;
    bottom: 0;
    .van-checkbox {
      float: right;
      font-size: px2rem(15);
      margin-right: px2rem(5);
      margin-top: px2rem(5);
      z-index: 1000;
    }
  }
}
</style>