import myrequest from '@/utils/axios'
import MD5 from 'js-md5';
import { Base64 } from 'js-base64';
import CryptoJS from 'crypto-js/crypto-js'

// 海康视频 （开发中）
export function hikVideo (params) {
  return myrequest({
    url: '/cameraLink/v1',
    method: 'post',
    data: params,
    headers: {
      "Content-Type": 'multipart/form-data; boundary=----WebKitFormBoundaryQlBUEO5BA0KmZqyH'
    }
  })
}



function stringToByte (str) {
  var bytes = new Array();
  var len, c;
  len = str.length;
  for (var i = 0; i < len; i++) {
    c = str.charCodeAt(i);
    if (c >= 0x010000 && c <= 0x10FFFF) {
      bytes.push(((c >> 18) & 0x07) | 0xF0);
      bytes.push(((c >> 12) & 0x3F) | 0x80);
      bytes.push(((c >> 6) & 0x3F) | 0x80);
      bytes.push((c & 0x3F) | 0x80);
    } else if (c >= 0x000800 && c <= 0x00FFFF) {
      bytes.push(((c >> 12) & 0x0F) | 0xE0);
      bytes.push(((c >> 6) & 0x3F) | 0x80);
      bytes.push((c & 0x3F) | 0x80);
    } else if (c >= 0x000080 && c <= 0x0007FF) {
      bytes.push(((c >> 6) & 0x1F) | 0xC0);
      bytes.push((c & 0x3F) | 0x80);
    } else {
      bytes.push(c & 0xFF);
    }
  }
  return bytes;


}



function s (x) {
  return x.charCodeAt(0);
}
function getBytes (data) {
  data = JSON.stringify(data)
  let list = data.split('').map(s);
  return list
}


export function hikDemo (params) {
  // let url = '/artemis/api/v1/oauth/token'
  let url = '/artemis/api/video/v1/cameras/previewURLs'
  let method = 'POST'
  let Accept = 'text/html;charset=UTF-8'
  let Content_MD5 = Base64.encode(MD5(getBytes(params)))
  let Content_Type = 'application/json'
  let date = encodeURIComponent(new Date())
  let X_Ca_Key = '28988515'
  // let X_Ca_Key = '7a110289fed0860511fd0a960f2e5331dfa8cc3f'
  let X_Ca_Signature_Headers = 'x-ca-key,x-ca-nonce,x-ca-timestamp'

  let httpHeaders = method + "\n"
    + Accept + "\n"
    + Content_Type + "\n"
    + 'x-ca-key:' + X_Ca_Key + "\n"
    + "x-ca-nonce:" + '2222' + "\n"
    + "x-ca-timestamp:" + '2222' + "\n"
    + url

  console.log("getBytes(params)", httpHeaders, stringToByte(httpHeaders));
  let appSecret = 'htSW6O2HNSKmWSsGoQuW'
  let res1 = CryptoJS.HmacSHA256(httpHeaders, appSecret)
  console.log(res1);
  let Signature = CryptoJS.enc.Base64.stringify(res1)
  console.log("httpHeaders + url", httpHeaders + url);
  console.log("SignatureSignature", Signature);

  const head = {
    'Accept': Accept,
    // 'Content-MD5': Content_MD5,
    'Content-Type': 'application/json',
    // 'Date': date,
    "x-ca-nonce": '2222',
    "x-ca-timestamp": '2222',
    'x-ca-key': X_Ca_Key,
    'x-ca-signature': Signature,
    'x-ca-signature-headers': X_Ca_Signature_Headers,
  }

  console.log("head", head);

  return myrequest({
    url: "http://192.168.1.161/artemis/api/video/v1/cameras/previewURLs",
    method: method,
    data: params,
    headers: head
  })
}